// =============================================================================
// > MODULES - GUI: SHADOWBOX
// =============================================================================

.shadowbox {
    @extend %cover;
    position: fixed;
    background: rgba(black, .75);
    z-index: 9999;
    display: none;

    &__content {
        @extend %cover;
        display: flex;
        align-items: center;
        justify-content: center;

        // Loading content
        &::before {
            @include fa(circle-notch);
            @include size(30px);
            @include font(20px 30px center white);
            @include absolute(top 50% left 50%);
            transform: translate(-50%, -50%);
            animation: spin 2s linear infinite;
            margin-left: -15px;
            margin-top: -15px; } }

    &__close {
        @include absolute(top 20px right 20px);
        @include size(50px);
        @include font(30px 50px white center);
        text-indent: -9999px;
        cursor: pointer;
        transition: all .25s ease;
        z-index: 100;

        &:hover {
            @include font(primary); }

        &::before {
            @include fa(times);
            @extend %cover;
            text-indent: 0; } }


    // ==================================================
    // > CONTENT TYPES
    // ==================================================
    iframe {
        @include size(60%);
        position: relative;
        @include screen(xs) {
            @include size(90%); } } }



