// =============================================================================
// > MODULES: TXT - QUOTES
// =============================================================================

blockquote,
.blockquote {
    padding: 0 20px;
    margin: 30px 50px 80px 30px;
    @include screen(sm) {
        margin-right: 0; }
    @include screen(xs) {
        margin: 0 0 40px 0;
        padding: 0; }

    cite,
    &__author {
        display: block;
        background: color(light-grey);
        position: relative;

        @include font(semibold white);
        margin: 0 -20px;
        padding: 10px 20px 10px 60px;

        font-style: normal;
        color: lighten(color(dark-grey), 10%);

        @include screen(xs) {
            margin: 200px 0 0;
            padding: 10px 20px; } }

    &__picture {
        border-radius: 50%;
        @include size(90px);
        @include absolute(top 50% left 0);
        transform: translate(-50%, -50%);
        border: 3px solid color(light-grey);
        @include screen(xs) {
            @include pos(top -180px left 50%);
            transform: translate(-50%, 0);
            @include size(170px); } }

    &__quote {
        font-style: italic;
        position: relative;
        margin: 30px 40px;
        @include screen(xs) {
            margin: 0; }

        &::before,
        &::after {
            @include font(30px light-grey); }
        &::before {
            @include fa(quote-left);
            @include absolute(top -20px left -40px); }
        &::after {
            @include fa(quote-right);
            @include absolute(right -40px bottom -20px); } } }



